import React from 'react';
import { withRouter } from 'react-router';
import { Form, Input, Icon, Select, Modal } from 'antd';
import { get, pick } from 'lodash';
import api from '../Services/api';
import StepBars from '../Components/StepsBar';
import './LoanSubmitPage.css';

const { Option } = Select;

const { confirm } = Modal;

const formItemLayout = {
  labelCol: { offset: 1, span: 10 },
  wrapperCol: { span: 12 },
  labelAlign: 'left',
};

class LoanSubmitPage extends React.Component {
  constructor(props){
    super(props);
    this.state = { 
      steps: 1,
      loanAmount: 1000000,
      occupation: 'Labour',
      salary: 65000,
      installment: 15000,
      interest: 7,
      data: {},
    };
  }

  async componentDidMount() {
    const result = await api.fetchProfile();
    this.setState({ data: get(result, ['data', 'profile'], {}) });
  }

  renderFormItem = (label, value, disabled = true) => (
    <Form.Item key={label} label={label}>
      <Input value={value} disabled={disabled}/>
    </Form.Item>
  );

  handleNextStep = () => {
    confirm({
      title: '',
      onOk: () => this.props.history.push('/loan-summary'),
    });
  };

  handleBackStep = () => this.props.history.push('/loan-compare');

  renderPersonalInfo () {
    const profile = pick(this.state.data, [
      'citizenID',
      'thaiFirstName',
      'thaiLastName',
      'birthDate',
      'genderCode',
      'mobile',
      'email',
    ]);
    return (
      <div className="loan-submit-subform">
        <h3 className="title"></h3>
        {this.renderFormItem('', `${profile.thaiFirstName || ''} ${profile.thaiLastName || ''}`)}
        {this.renderFormItem('', `${profile.birthDate || '01/01/1990'}`)}
        {this.renderFormItem('', `${profile.mobile || '087777771'}`, false)}
        {this.renderFormItem('', `${profile.email || 'email@email.com'}`, false)}
      </div>
    )
  }

  renderIncome = () => {
    return (
    <div className="loan-submit-subform">
      <h3 className="title"></h3>
      <Form.Item label="">
        <Select
        placeholder="Select a option and change input text above"
        onChange={(value) => this.setState({ occupation: value})}
        value={this.state.occupation}
        disabled
        >
          <Option value="Labour"></Option>
          <Option value="Owner"></Option>
        </Select>
      </Form.Item>
      <Form.Item label="">
        <Input
          prefix={<Icon type="dollar" style={{ color: 'rgba(0,0,0,.25)' }} />}
          onChange={(e) => this.setState({ salary: e.target.value })}
          value={this.state.salary}
          type="number"
          addonAfter=" "
          placeholder=""
          disabled
        />
      </Form.Item>
    </div>);
  }

  renderLoan = () => {
    return (
      <div className="loan-submit-subform">
        <h3 className="title"></h3>
        {this.renderFormItem('', 'Plan 1')}
        <Form.Item label="" >
          <Input
            disabled
            prefix={<Icon type="dollar" style={{ color: 'rgba(0,0,0,.25)' }} />}
            onChange={(e) => this.setState({ installment: e.target.value })}
            value="1000000"
            type="number"
            addonAfter=" "
          />
        </Form.Item>
        <Form.Item label="">
          <Input
            prefix={<Icon type="dollar" style={{ color: 'rgba(0,0,0,.25)' }} />}
            onChange={(e) => this.setState({ interest: e.target.value })}
            value={this.state.interest}
            type="number"
            addonAfter=" % "
            disabled
          />
        </Form.Item>
        <Form.Item label="" >
          <Input
            disabled
            prefix={<Icon type="dollar" style={{ color: 'rgba(0,0,0,.25)' }} />}
            onChange={(e) => this.setState({ installment: e.target.value })}
            value={Math.ceil(1000000 * 0.007 * 12)}
            type="number"
            addonAfter="  / "
          />
        </Form.Item>
        <Form.Item label="" >
          <Input
            disabled
            onChange={(e) => this.setState({ installment: e.target.value })}
            value={Math.ceil(1000000/15000)}
            type="number"
            addonAfter=" "
          />
        </Form.Item>
        <Form.Item label="" >
          <Input
            disabled
            onChange={(e) => this.setState({ installment: e.target.value })}
            value="15000"
            type="number"
            addonAfter="  / "
          />
        </Form.Item>
      </div>
    );
  }

  render(){
    return(
      <div className="loanSubmitPage">
        <div className="steps-bar">
          <StepBars current={3} />
        </div>
        <div className="loan-submit-body">
          <Form {...formItemLayout} onSubmit={this.handleNextStep}>
            {this.renderPersonalInfo()}
            {this.renderIncome()}
            {this.renderLoan()}
          </Form>
          <button className="next-button" onClick={this.handleNextStep}></button>
          <button className="back-button" onClick={this.handleBackStep}></button>
        </div>
      </div>
    );
  }
}

const WrappedLoanSubmitPage = Form.create({ name: 'loan_submit' })(LoanSubmitPage);

export default withRouter(WrappedLoanSubmitPage);